<template>
    <h1 class="timer">
        {{ time }}
    </h1>
    <div class="textToType">
        <div class="textToType1">
            {{ testText }}
        </div>

        <div class="resultDisplay">
        </div>
        
        <textarea name="typingArea" id="" v-on:keydown="startTimer" spellcheck="false"></textarea>
    </div>

    <div class="typing-result" v-if="typingFinished">
        Your typing speed is {{ typingSpeed }} WPM
    </div>

    <button @click="testApi">
        Try Api
    </button>
</template>

<script>
    export default {
        data () {
            return {
                testText: 'this is a sample generated text that i have written to fill up some space this is a sample generated text that i have written to fill up some space this is a sample generated text that i have written to fill up some space',
                typingStarted: false,
                time: 10,
                typingFinished: false,
                typingSpeed: 0
            }
        },
        methods: {
            startTimer: function(){
                if (!this.typingStarted){
                    this.typingStarted = true;
                    const timer = setInterval(() => {
                        if (this.time > 0){
                            this.time--;
                        }else{
                            this.typingFinished = true
                            this.calculateTypingSpeed();
                            clearInterval(timer)
                        }
                    }, 1000);
                }
                this.addSpanAroundText()
            },
            addSpanAroundText: function (){
                setTimeout(() => {
                    const enteredText = document.querySelector('textarea').value;

                    const resultDisplayElement = document.querySelector('.resultDisplay')
                    resultDisplayElement.innerHTML = ''

                    for (let i = 0; i < enteredText.length; i++){
                        if (enteredText[i] == this.testText[i]){
                            resultDisplayElement.innerHTML += `<span class="rightWord";">${enteredText[i]}</span>`
                        }else{
                            resultDisplayElement.innerHTML += `<span style="color: red;">${this.testText[i]}</span>`
                        }
                    }
                }, 10);
            },
            calculateTypingSpeed: function() {
                const wordsTyped = document.querySelector('textarea').value.split(' ')
                const availablewords = this.testText.split(' ')

                let charsInTotalCorrectWords = 0;

                for (let word of wordsTyped){
                    for (let word2 of availablewords){
                        if (word === word2){
                            charsInTotalCorrectWords += word.length;
                            break
                        }
                    }
                }

                this.typingSpeed = Math.round(((charsInTotalCorrectWords) / 5) * 6)
            },
            testApi: function(){
                const result = fetch("https://localhost:7093/api/Student", {
                })
                console.log(result)
            }
        }
    }
</script>

<style>
header {
    font-family: sans-serif;
    font-size: 40px;
}

.rightWord {
    color: rgb(61, 255, 61);
}

.timer {
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
    text-align: left;
    margin-top: 200px;
    margin-bottom: 0px;
}

.textToType {
    /* position: absolute;
    width: 80vw; */
    display: grid;

    margin-top: 5px;
    font-size: 30px;
    margin-left: auto;
    margin-right: auto;
    width: 80vw;
    text-align: left;
}

.textToType1 {
    grid-column: 1;
    grid-row: 1;

    opacity: 50%;

    font-family: 'Roboto Mono', monospace;
}


.resultDisplay {
    grid-column: 1;
    grid-row: 1;

    width: 80vw;
    margin-left: auto;
    margin-right: auto;

    font-family: 'Roboto Mono', monospace;
}


textarea {
    grid-column: 1;
    grid-row: 1;
    color: transparent;
    caret-color: black;

    width: 80vw;
    font-size: 30px;

    font-family: 'Roboto Mono', monospace;

    border: none;
    padding: 0;
    white-space: normal;
    text-align: left;
    background: transparent;
    z-index: 5;
}

textarea:focus-visible {
    outline: none;
}

.typing-result {
    font-family: 'Roboto Mono', monospace;
    
    margin: 30px;
    font-size: 30px;
}

</style>